}

.temperament {
  font-weight: 700;
}

.breed {
  font-size: 30px;
  font-weight: 700;
}

@keyframes rotate {
  0% {
    transform: rotate(-.08turn);
  }

  100% {
    transform: rotate(-1.08turn);
  }
}

